import Vue from "vue";
import Router from "vue-router";

const index = () => import("../components/index.vue");
const main = () => import("../components/main.vue");
const movie = () => import("../components/movie.vue");

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/main',
      name: 'main',
      component: main,
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/movie',
      name: 'movie',
      component: movie
    }
  ]
});



