<template>
  <div id="app">
    <keep-alive>     <!--使用keep-alive会将页面缓存-->
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive> 
    <router-view v-if="!$route.meta.keepAlive"></router-view>
    <!-- <router-view></router-view> -->
  </div>
</template>

<script>
export default {};
</script>

<style scoped lang="less">
#app {
  height: 100%;
  overflow: hidden;
}
</style>



